import styled from 'styled-components';
import React from 'react';
import { useParams, useHistory } from 'react-router-dom';
import { useRecords } from 'hooks/useRecords';
import { useTags } from 'hooks/useTags';
import Icon from 'components/Icon';
import moment from 'moment';
import { Button } from 'components/Button';
import { showConfirm } from 'components/confirm';
import { message } from 'antd';

const Wrapper = styled.section`
    display:flex;
    flex-direction:column;
    height:100vh;
    >header{
        position:relative;
        display:flex;
        flex-shrink:0;
        height:60px;
        align-items:center;
        justify-content:center;
        background:var(--skin-color);
        font-size:20px;
        .back-icon{
            position:absolute;
            left:10px;
            top:0;
            bottom:0;
            margin:auto;
        }
        .icon{
            width:24px;
            height:24px;
        }
        >span{
            margin-left:10px;
        }
    }
    >main{
        flex-shrink:1;
        height:100%;
        padding:20px 20px;
        background:#fff;
        overflow:auto;
    }
    >footer{
        display:flex;
        align-items:center;
        height:50px;
        border-top:1px solid var(--border-color);
        >Button{
            width:50%;
            height:100%;
            background:#fff;
            cursor:pointer;
            &:active{
                background:var(--bg-color);
            }
        }
        >.line{
            width:1px;
            height:60%;
            background:var(--border-color);
        }
    }
`;
const ItemWrapper = styled.div`
    display:flex;
    border-bottom:1px solid var(--border-color);
    padding:10px;
    >.title{
        flex-shrink:0;
        padding-right:30px;
        color:#8c8a8a;
    }
    >.content{
        word-wrap:break-word; 
        word-break:break-all; 
    }
`;
const getDay = (dayNumber: number) => {
    const days = [
        "",
        "",
        "",
        "",
        "",
        "",
        ""
    ];
    return days[dayNumber];
}

const RecordDetail = () => {
    let id = parseInt(useParams<{ id: string }>().id);
    const { records, deleteRecord } = useRecords();
    const { findTag } = useTags();
    const history = useHistory();
    const record = records.find(v => v.id === id);
    const tag = findTag(record?.tagIds[0] || -1)
    const date = moment(record?.createdDate);
    const items = [
        { text: "", value: record?.category === "-" ? "" : "" },
        { text: "", value: record?.amount + "" },
        { text: "", value: date.format("YYYYMD") + " " + getDay(date.day()) },
        { text: "", value: record?.note }
    ];
    const deleteBill = () => {
        showConfirm({
            title: "",
            content: "",
            okFn: () => {
                if (deleteRecord(id)) {
                    message.success("");
                    history.goBack();
                } else {
                    message.error("");
                }
            },
        })
    };
    return (<Wrapper>
        <header>
            <Icon className="back-icon" name="left" onClick={() => { history.goBack() }} />
            <Icon name={tag?.icon} />
            <span>
                {tag?.name}
            </span>
        </header>
        <main>
            {
                items.map((item, i) => {
                    return (
                        <ItemWrapper key={i}>
                            <span className="title">{item.text}</span>
                            <span className="content">{item.value}</span>
                        </ItemWrapper>
                    );
                })
            }
        </main>
        <footer>
            <Button onClick={() => { history.push(`/money/${id}`); }}></Button>
            <div className="line"></div>
            <Button onClick={deleteBill}></Button>
        </footer>
    </Wrapper>);

}

export { RecordDetail };