
import { DatePicker, List } from 'antd-mobile';
import Modal from 'antd/lib/modal';
import React, { useState } from 'react';
import { message } from 'antd';
import moment from 'moment';

type Props = {
    visible: boolean;
    setVisible: (visible: boolean) => void,
    okFn: (startDate: Date, endDate: Date) => void
}
const dateCheck = (startDate: moment.Moment, endDate: moment.Moment) => {
    if (startDate > endDate) {
        message.error("");
        return false;
    }
    return true;
}
const DateModal = (props: Props) => {
    const { visible, setVisible, okFn } = props;
    // 
    const [startDate, setStartDate] = useState(new Date());
    const [endDate, setEndDate] = useState(new Date());
    return (
        <Modal
            title=""
            visible={visible}
            okText=""
            cancelText=""
            onOk={() => { okFn(startDate, endDate) }}
            onCancel={() => { setVisible(false) }}
        >
            <div>
                <DatePicker
                    mode="date"
                    title=""
                    extra="Optional"
                    value={startDate}
                    onChange={date => {
                        if (!dateCheck(moment(date), moment(endDate))) {
                            return;
                        }
                        setStartDate(date)
                    }}
                >
                    <List.Item arrow="horizontal"></List.Item>
                </DatePicker>
            </div>
            <div>
                <DatePicker
                    mode="date"
                    title=""
                    extra="Optional"
                    value={endDate}
                    onChange={date => {
                        if (!dateCheck(moment(startDate), moment(date))) {
                            return;
                        }
                        setEndDate(date)
                    }}
                >
                    <List.Item arrow="horizontal"></List.Item>
                </DatePicker>
            </div>
        </Modal>
    )
}
export { DateModal };