import { useEffect, useState } from "react";
import { createId } from "lib/createId";
import { useUpdate } from "./useUpdate";
const useTags = () => {
  const [tags, setTags] = useState<
    { id: number; name: string; icon: string; category: Category }[]
  >([]);
  useEffect(() => {
    let localTags = JSON.parse(window.localStorage.getItem("tags") || "[]");

    if (localTags.length === 0) {
      localTags = [
        { id: createId(), name: "", category: "-", icon: "dress" },
        { id: createId(), name: "", category: "-", icon: "food" },
        { id: createId(), name: "", category: "-", icon: "traffic" },
        { id: createId(), name: "", category: "-", icon: "travel" },
        { id: createId(), name: "", category: "+", icon: "cash" },
        { id: createId(), name: "", category: "+", icon: "allowance" },
        { id: createId(), name: "", category: "-", icon: "sock" },
        { id: createId(), name: "", category: "-", icon: "message" },
        { id: createId(), name: "", category: "-", icon: "social-contact" },
        { id: createId(), name: "", category: "+", icon: "salary" },
        { id: createId(), name: "", category: "+", icon: "bonus" },
        { id: createId(), name: "", category: "+", icon: "part-time" },
      ];
    }
    setTags(localTags);
  }, []); // 
  useUpdate(() => {
    window.localStorage.setItem("tags", JSON.stringify(tags));
  }, tags);
  const findTag = (id: number) => tags.filter((tag) => tag.id === id)[0];
  const findTagIndex = (id: number) => {
    let result = -1;
    for (let i = 0; i < tags.length; i++) {
      if (tags[i].id === id) {
        result = i;
        break;
      }
    }
    return result;
  };
  const getTags = (category: Category) => {
    tags.filter((tag) => tag.category === category);
  };
  const updateTag = ({
    id,
    name,
    category,
  }: {
    id: number;
    name: string;
    category: Category;
  }) => {
    setTags(
      tags.map((tag) => (tag.id === id ? { ...tag, name, category } : tag))
    );
  };
  const deleteTag = (id: number): boolean => {
    setTags(tags.filter((tag) => tag.id !== id));
    return true;
  };
  const addTag = (data: { name: string; category: Category }) => {
    setTags([...tags, { id: createId(), icon: "other", ...data }]);
  };
  const getName = (id: number) => {
    const tag = tags.filter((t) => t.id === id)[0];
    return tag ? tag.name : "";
  };
  return {
    tags,
    getTags,
    getName,
    addTag,
    setTags,
    findTag,
    updateTag,
    findTagIndex,
    deleteTag,
  };
};

export { useTags };
